import { DomaineType, DomaineEntity } from "../../../entities/domaine.entity";
import { EltDossierEntity } from "../../../entities/eltDossier.entity";
import { EltRubriqueEntity } from "../../../entities/eltRubrique.entity";
import { MedecinEntity } from "../../../entities/medecin.entity";
import { OrganismeEntity } from "../../../entities/organisme.entity";
import { PatientEntity } from "../../../entities/patient.entity";
import { ResponseType } from "../../../entities/reponse.entity";
import { RubriqueEntity } from "../../../entities/rubrique.entity";
import { API_ROUTES } from "../../../shared/api/api.route";
import { customFetch } from "../../../shared/customFetch";

// ================== DEMANDES D'ADHESION ===========================================
export const fetchDemandes = (): Promise<ResponseType<any>> => {
  return customFetch.get(API_ROUTES.DEMANDES.BASE); // retourne les dossiers du medecins qui a effectu la demande
};

export const accepterDemande = (
  demandeToPost: any
): Promise<ResponseType<any>> => {
  return customFetch.post(API_ROUTES.DEMANDES.ACCEPT, demandeToPost); // retourne les dossiers du medecins qui a effectu la demande
};

export const rejeterDemande = (
  demandeToPost: any
): Promise<ResponseType<any>> => {
  return customFetch.post(API_ROUTES.DEMANDES.REJECT, demandeToPost); // retourne les dossiers du medecins qui a effectu la demande
};

// ================== MEDECINS ===========================================
export const fetchMedecins = (): Promise<ResponseType<MedecinEntity>> => {
  return customFetch.get(API_ROUTES.MEDECINS.BASE); // retourne les dossiers du medecins qui a effectu la demande
};

export const createMedecin = (
  medecinToCreate: any
): Promise<ResponseType<any>> => {
  return customFetch.post(API_ROUTES.MEDECINS.BASE, medecinToCreate);
};

export const createOrganismeMedecin = (
  orgMedToCreate: any
): Promise<ResponseType<any>> => {
  return customFetch.post(API_ROUTES.MEDECINS.BASE, orgMedToCreate);
};

// ================== ELTRUBRIQUES ================================================
export const fetchEltRubrique = (): Promise<
  ResponseType<EltRubriqueEntity>
> => {
  return customFetch.get(API_ROUTES.PARAMETRES.BASE("eltRubrique"));
};

// ================== ORGANISME ======================================================
export const fetchOrganisme = (): Promise<ResponseType<OrganismeEntity>> => {
  return customFetch.get(API_ROUTES.PARAMETRES.BASE("organisme"));
};

// ===================== PAYS ===========================
export const fetchPays = (): Promise<ResponseType<any>> => {
  return customFetch.get(API_ROUTES.PARAMETRES.BASE("pays"));
};

// ================== PATIENTS ===========================================
export const fetchPatients = (): Promise<ResponseType<PatientEntity>> => {
  return customFetch.get(API_ROUTES.PATIENTS.BASE); // retourne les dossiers du medecins qui a effectu la demande
};

export const createPatient = (
  patientToCreate: any
): Promise<ResponseType<any>> => {
  return customFetch.post(API_ROUTES.PATIENTS.BASE, patientToCreate);
};

//========================== DOMAINES ===================================
export const fetchDomaines = (
  domaine: DomaineType
): Promise<ResponseType<DomaineEntity>> => {
  return customFetch.get(API_ROUTES.DOMAINES.BASE(domaine));
};

export const createDomaine = (
  domaine: DomaineType,
  domaineToCreate: string
): Promise<ResponseType<DomaineEntity>> => {
  return customFetch.post(API_ROUTES.DOMAINES.BASE(domaine), domaineToCreate);
};

export const deleteDomaine = (
  domaine: DomaineType,
  id: string
): Promise<ResponseType<DomaineEntity>> => {
  return customFetch.delete(API_ROUTES.DOMAINES.BASEID(domaine, id));
};

export const updateDomaine = (
  domaine: DomaineType,
  id: string,
  domaineToUpdate: string
): Promise<ResponseType<DomaineEntity>> => {
  return customFetch.put(
    API_ROUTES.DOMAINES.BASEID(domaine, id),
    domaineToUpdate
  );
};

// ================== DOSSIERS ============================================
export const fetchDossier = (id?: string): Promise<ResponseType<any>> => {
  return id
    ? customFetch.get(API_ROUTES.DOSSIERS.BASEID(id))
    : customFetch.get(API_ROUTES.DOSSIERS.BASE); // retourne les dossiers du medecins qui a effectu la demande
};

export const createDossier = (
  dossierToCreate: any
): Promise<ResponseType<any>> => {
  return customFetch.post(API_ROUTES.DOSSIERS.BASE, dossierToCreate);
};

// creer de nouveaux elements de dossier
export const completeDossier = async (
  dataToComplete: any
): Promise<ResponseType<any>> => {
  return customFetch.post(API_ROUTES.DOSSIERS.COMPLETE, dataToComplete);
};

export const deleteDossier = (dossierID: any): Promise<ResponseType<any>> => {
  return customFetch.delete(API_ROUTES.DOSSIERS.BASEID(dossierID));
};

// mettre  jour le dossier existant
export const updateDossier = async (
  dossierID: string,
  eltDossierToUpdate: any
): Promise<ResponseType<any>> => {
  return customFetch.put(
    API_ROUTES.DOSSIERS.BASEID(dossierID),
    eltDossierToUpdate
  );
};

// ================== ELT DOSSIER =================================================
export const fetchEltDossier = (): Promise<ResponseType<EltDossierEntity>> => {
  return customFetch.get(API_ROUTES.ELTDOSSIERS.BASE);
};

// ================== RUBRIQUES =================================================
export const fetchRubrique = (): Promise<ResponseType<RubriqueEntity>> => {
  return customFetch.get(API_ROUTES.PARAMETRES.BASE("rubrique"));
};
