export const getQualification = (val: string) => {
  const localEltRub = localStorage.getItem("eltRubriques");
  const eltRubriques = localEltRub
    ? (JSON.parse(localEltRub) as EltRubriqueEntity[])
    : [];

  return eltRubriques.find(
    (elt: EltRubriqueEntity) =>
      elt.rubrique === CODE_RUBRIQUES.QUALIFICATION && elt.id === val
  );
};
