import { DomaineType } from "../../entities/domaine.entity";

export const BASE_URL = "http://localhost:3001/rct-api/";
// export const BASE_URL =
//   "https://rct-api-cmr-e0acf1a3010f.herokuapp.com/rct-api/";

// export const BASE_URL =
//   "https://rct-front-cmr-96a5e486add5.herokuapp.com/rct-api/";

export const API_ROUTES = {
  DOMAINES: {
    // donnes de base
    BASE: (domaine: DomaineType) => `${BASE_URL}domain/${domaine}`,
    BASEID: (domaine: DomaineType, id: string) =>
      `${BASE_URL}domain/${domaine}/${id}`,
  },
  DOSSIERS: {
    BASE: `${BASE_URL}gestion/dossier`,
    COMPLETE: `${BASE_URL}gestion/dossier/complete`,
    BASEID: (id: string) => `${BASE_URL}gestion/dossier/${id}`,
  },
  ELTDOSSIERS: {
    BASE: `${BASE_URL}gestion/eltDossier`,
    BASEID: (id: string) => `${BASE_URL}gestion/eltDossier/${id}`,
  },
  PATIENTS: {
    BASE: `${BASE_URL}gestion/patient`,
    BASEID: (id: string) => `${BASE_URL}gestion/patient/${id}`,
  },
  ORGMED: {
    BASE: `${BASE_URL}admin/org-med`,
    BASEID: (id: string) => `${BASE_URL}admin/org-med/${id}`,
  },
  MEDECINS: {
    BASE: `${BASE_URL}admin/medecin`,
    BASEID: (id: string) => `${BASE_URL}admin/medecin/${id}`,
  },
  DEMANDES: {
    BASE: `${BASE_URL}demandes/adhesion`,
    BASEID: (id: string) => `${BASE_URL}demandes/adhesion${id}`,
    ACCEPT: `${BASE_URL}demandes/adhesion/accept`,
    REJECT: `${BASE_URL}demandes/adhesion/rejet`,
  },
  PARAMETRES: {
    BASE: (paramName: PARAM_NAMES) => `${BASE_URL}parametres/${paramName}`,
    BASEID: (paramName: PARAM_NAMES, id: string) =>
      `${BASE_URL}parametres/${paramName}/${id}`,
  },
};

export type PARAM_NAMES =
  | "continent"
  | "pays"
  | "region"
  | "departement"
  | "arrondissement"
  | "rubrique"
  | "eltRubrique"
  | "organisme";
