import "./App.css";
import "antd/dist/reset.css";
import "antd/dist/antd.js";
import "antd/dist/antd.min.js";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import { Suspense, useEffect, useState, lazy } from "react";
import {
  fetchEltRubrique,
  fetchMedecins,
  fetchOrganisme,
  fetchPays,
} from "./modules/admin/network";
import { CODE_RUBRIQUES } from "./shared/rubrique";
import { Spin, notification } from "antd";

const LoginPage = lazy(() => import("./modules/login/pages"));
const AdminRoutes = lazy(() => import("./modules/admin/routes"));
const ComingSoonPage = lazy(() => import("./modules/shared/ComingSoon"));
const NotFoundPage = lazy(() => import("./modules/shared/NotFound"));

function App() {
  const [isConnected, setIsConnected] = useState(true);

  useEffect(() => {
    console.log("fetch all stateful data : pays, organismes, eltRubriques");
    try {
      fetchPays()
        .then((data) => {
          if (data.success) {
            localStorage.setItem("pays", JSON.stringify(data.result));
          }
        })
        .catch((err) => {
          throw err;
        });
      fetchOrganisme()
        .then((data) => {
          if (data.success) {
            const formationSanitaires = data.result.filter(
              (elt) => elt.type_organisme === CODE_RUBRIQUES.FORMATION_SANITAIRE
            );
            localStorage.setItem(
              // for sidebar also
              "formationSanitaires",
              JSON.stringify(formationSanitaires)
            );
            localStorage.setItem("organismes", JSON.stringify(data.result));
          }
        })
        .catch((err) => {
          throw err;
        });
      fetchEltRubrique()
        .then((data) => {
          if (data.success) {
            localStorage.setItem("eltRubriques", JSON.stringify(data.result));
          }
        })
        .catch((err) => {
          throw err;
        });
      fetchMedecins()
        .then((data) => {
          // only for sidebar
          if (data.success) {
            console.log("get medecins ");
            localStorage.setItem("praticiens", JSON.stringify(data.result));
          }
        })
        .catch((err) => {
          throw err;
        });
    } catch {
      console.log("unable to connect to the database");
      setIsConnected(false);
      notification.error({
        message: "Erreur de connexion",
        description:
          "Dsol, nous ne parvenons pas  vous connecter au serveur. Veuillez reessayer ulterieurement.",
      });
    }
  }, []);

  return (
    <BrowserRouter>
      <Suspense
        fallback={
          <div
            style={{
              alignItems: "center",
              display: "flex",
              justifyContent: "center",
              position: "absolute",
              top: 0,
              bottom: 0,
              right: 0,
              left: 0,
            }}
          >
            <Spin spinning={true} size="large" style={{ margin: "auto" }}>
              {" "}
              Chargement...
            </Spin>
          </div>
        }
      >
        <Switch>
          <Route exact path="/" component={LoginPage} />
          <Route path="/admin" render={() => <AdminRoutes />} />
          <Route path="/next" component={ComingSoonPage} />
          <Route path="*" component={NotFoundPage} />
        </Switch>
      </Suspense>
    </BrowserRouter>
  );
}

export default App;
