export const customFetch = {
  get: async (url: string) => {
    return fetch(url)
      .then((res) => res.json())
      .catch((error) => {
        console.log("an error occured ", error);
        return {
          success: false,
          message: errorMessage1,
          result: error,
        };
      });
  },

  post: async (url: string, data: unknown) => {
    return fetch(url, {
      method: "post",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .catch((error) => {
        // console.log("an error occured ", error);
        return {
          success: false,
          message: errorMessage1,
          result: error,
        };
      });
  },

  put: async (url: string, data: unknown) => {
    return fetch(url, {
      method: "put",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .catch((error) => {
        // console.log("an error occured ", error);
        return {
          success: false,
          message: errorMessage1,
          result: error,
        };
      });
  },

  delete: async (url: string) => {
    return fetch(url, {
      method: "delete",
      headers: { "Content-Type": "application/json" },
    })
      .then((res) => res.json())
      .catch((error) => {
        console.log("an error occured ", error.type);
        return {
          success: false,
          message: errorMessage1,
          result: error,
        };
      });
  },
};

const errorMessage1 =
  "Une erreur est survenue. Verifier votre connexion internet. Si le problme persiste, contacter votre administrateur.";
